.footer{
  .footer__bg {
    padding-top: 2px; //  border 
    background: linear-gradient(270deg, rgba(192, 221, 255, 0.6) 0%, rgba(240, 247, 255, 0.6) 100%); // 
  };
  .footer__text{
    display: block; // 
    padding-top: 10px; //  border 

    text-align: center; // 
    color: #BFBFBF;

    background-color: #F0F7FF; //  padding  border 
    @include screen-lg{
      padding-top: 22px;
    };
  };
  .footer__link {
    &:hover {
      transform: scale(1.1);
    };
    &:active {
      transform: scale(1.1);
    };
  };
};