// transition
.transition-300 {
  transition: all .3s;
}

// focus


// border

// navbar mobile
.burger-show {
  @include screen-lg {
    height: auto;
    position: static;
    margin-top: 0;
  };
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  margin-top: 80px;
  z-index: 10;
  background: #fff;
}

// background
.background-gradient-linear {
  background: linear-gradient(135deg, #3B7FCF 0%, #64ABFF 100%);
  transition: all .5s;
  &:active {
    color: #3B7FCF !important;
    background: linear-gradient(135deg, rgba(192, 221, 255, 0.6) 0%, rgba(240, 247, 255, 0.6) 100%);
  }
  @include screen-lg {
    &:hover {
      color: #3B7FCF !important;
      background: linear-gradient(135deg, rgba(192, 221, 255, 0.6) 0%, rgba(240, 247, 255, 0.6) 100%);
    };
  }
}

.background-gradient-linear-active { // 
  color: #3B7FCF !important;
  background: linear-gradient(135deg, rgba(192, 221, 255, 0.6) 0%, rgba(240, 247, 255, 0.6) 100%);
  transition: all .5s;

  &:active {
    color: #3B7FCF !important;
    background: linear-gradient(135deg, rgba(192, 221, 255, 0.6) 0%, rgba(240, 247, 255, 0.6) 100%);
  };
}

// box-shadow
.image-shadow {
  box-shadow: (0px 4px 20px rgba(0, 0, 0, 0.08));
}

.dropdown-shadow {
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.12);
}

// Editor-TinyMCE
.editor-form {
  .editor__form-btn {
    z-index: -1;
    transform: translateY(-50px);
  }
  &:hover{
    .editor__form-btn {
      z-index: auto;
      border-radius: 0;
      transform: translateY(0);
    }
  }
}
[role=application] {
  width: 100%;
}

// 
.h1-image {
  display: block;
  width: 175px;
  height: 48px;
  background-image: url('../images/logo.svg'); 
  //  Github Page  repository 
  overflow: hidden;
  text-indent: 101%;
  white-space: nowrap;
}

//  hover
.my-avatar {
  &:hover {
    .avatar-show {
      display: block;
    }
  }
}
.avatar-show{
  display: none;
  background: #fff;
}