.template-class {
  .dropdown-toggle {
    display: flex;
    align-items: center;
    justify-content: center;

    padding: 14px 16px;

    width: 160px;
    height: 48px;

    border-radius: 16px;
    &:focus {
      background-color: #F0F7FF; // dropdown 
      box-shadow: 0 0 0 .125rem #3B7FCF; //  border
    };
    &:active {
      background-color: #F0F7FF; // dropdown 
      box-shadow: 0 0 0 .125rem #3B7FCF; //  border
    };
    &::after{
      margin: 0;

      width: 24px;
      height: 24px;

      vertical-align: text-top; // svg 
      border: 0;
      -webkit-mask: url('../images/arrow_down.svg'); //  svg 
      mask: url('../images/arrow_down.svg'); //  svg 
      -webkit-mask-size: cover; //  svg 
      background-color: #7E7E7E; //  svg 
    };
    &:hover {
      &::after {
        background-color: #3B7FCF; //  svg hover 
      };
    };
    &:focus {
      &::after{
        background-color: #3B7FCF; //  svg focus 
      }
    };
  };
};