.personal-bg-image { //  
  background-image: url('../images/Geometric Background.jpg');
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
};

.aside-divider {
  border-top: 2px solid rgba(191, 191, 191, .3);
};

.account-shadow { // 
  box-shadow: 3px 5px 10px -3px #6c6b6b;
};

.setting-input { //  input 
  &:focus-visible {
    outline: 0;
  };
};

.aside-border-gradient { //  aside 
  background: linear-gradient(0deg, #3B7FCF 0%, #64ABFF 100%);
};